:root{--white:#fff;--black:#000;--red:#ff4f5e;--light-red:#fdc6c8;--green:#b7d43f;--valid-green:#59cbb7;--near-black:#4a4a4a;--grey:#e7e5e2;--darker-grey:#e6e5e3;--light-grey:#f3f2f0;--lighter-grey:#f9f9f9;--desert-storm:#f3f2f1;--warm-grey:#959595;--blue:#629ff4;--link-color:var(--blue);--override-z-index:3;--top-z-index:2;--middle-z-index:1;--bottom-z-index:0;--base-font-family:"Open Sans",sans-serif;--strong-font-family:"Zilla Slab",Georgia,Utopia,Charter,serif;--font-size:16px;--font-size-xs:12px;--font-size-lg:24px;--font-size-xl:32px;--font-size-xxl:34px;--standard-margin:0.3rem;--wide-desktop-width:1400px;--desktop-width:76rem;--old-desktop-width:55rem;--page-margin:1rem;--input-height:2.5rem;--spinner-height:10rem;--card-shadow:rgba(0,0,0,0.09) 0px 1px 6px,rgba(0,0,0,0.09) 0px 1px 4px;--transition-duration:0.2s;--transition-duration-slow:0.5s;--easing:cubic-bezier(0.4,0,0.2,1);--header-height:4rem;--nav-shadow-overhang:0px 2px 4px rgba(0,0,0,0.03);--dataset-item-margin:0.7rem;--profile-column-margin:30px}@keyframes fade-in{0%{opacity:0;pointer-events:none}to{opacity:1;pointer-events:all}}.md-block{display:none}@media (min-width:768px){.md-block{display:block}}@media (max-width:768px){.hidden-sm-down{display:none!important}}@media (max-width:992px){.hidden-md-down{display:none!important}}@media (min-width:768px){.hidden-md-up{display:none!important}}@media (min-width:992px){.hidden-lg-up{display:none!important}}.register-section .top{margin:0 auto;display:flex;flex-direction:row;justify-content:space-between;align-items:flex-start;max-width:76rem;max-width:var(--desktop-width)}@media (max-width:992px){.register-section .top{flex-direction:column}.register-section .top .cta-container{display:flex;flex-direction:column;align-items:center;text-align:center}}.register-section .top .images-container{position:relative;display:flex;flex-direction:column;align-items:center}@media (max-width:768px){.register-section .top .images-container{width:100%}}.register-section .top .images-container .mars{width:290px;z-index:1;z-index:var(--middle-z-index)}.register-section .top .images-container .screenshot{margin-top:50px;max-width:500px}@media (max-width:768px){.register-section .top .images-container .screenshot{display:none}}.register-section h1,.register-section h2{max-width:450px;width:100%}.register-section h1{margin-bottom:10px;font-family:Zilla Slab,Georgia,Utopia,Charter,serif;font-family:var(--strong-font-family);font-size:32px;font-size:var(--font-size-xl);font-weight:400}.register-section h2{margin-bottom:30px;font-family:Open Sans,sans-serif;font-family:var(--base-font-family);font-size:24px;font-size:var(--font-size-lg);font-weight:300}.register-section .button{border:none;max-width:270px;height:57px;background:#629ff4;background:var(--blue);color:#fff;color:var(--white);text-transform:none}.register-section .button:hover{border:1px solid #629ff4;border:1px solid var(--blue)}@media (min-width:768px){.register-section.flipped .top{flex-direction:row-reverse}.register-section.flipped .waves{transform:scaleX(-1) translateX(60%);z-index:0;z-index:var(--bottom-z-index)}.register-section.flipped .bg{right:0;left:auto}}.signup-info{margin-top:80px}@media (min-width:768px){.signup-info{max-width:550px}}.signup-info .tabs{border-bottom:2px solid #f3f2f0;border-bottom:2px solid var(--light-grey);position:relative;display:flex;font-size:12px;font-size:var(--font-size-xs);text-transform:uppercase;font-weight:600;letter-spacing:.4px}.signup-info .tabs .waves{position:absolute;bottom:-5px;width:100vw;pointer-events:none}.signup-info .tabs label{min-width:120px;color:#959595;color:var(--warm-grey);cursor:pointer}.signup-info .tabs div{padding-bottom:14px;position:relative;top:1px}.signup-info .tabs input{display:none}.signup-info .tabs :checked+div{border-bottom:2px solid #000;border-bottom:2px solid var(--black);color:#000;color:var(--black)}.signup-info .list-and-bg{position:relative}.signup-info .bg{width:100vw;height:100%;position:absolute;left:0;background-image:linear-gradient(180deg,#f3f2f1 3%,rgba(249,248,248,0));background-image:linear-gradient(180deg,var(--desert-storm) 3%,rgba(249,248,248,0))}.signup-info ul{margin-top:0;padding:30px 20px;position:relative;overflow:auto;list-style:none;background:#fff;background:var(--white)}@media (max-width:768px){.signup-info ul{max-height:50vh}}@media (min-width:768px){.signup-info ul{max-height:450px}}.signup-info li{padding:40px 80px 40px 30px;display:flex;flex-direction:row;color:#4a4a4a;color:var(--near-black);cursor:pointer}.signup-info li.active{color:#000;color:var(--black);background:#f3f2f1;background:var(--desert-storm)}.signup-info li :first-child{flex-shrink:0;width:30px}